<template>
  <div class="teams">
    <form @submit.prevent="handleSubmit">
      <div class="team-list">
        <div>
          <h2>Add Teams</h2>
          <label>
            Team Name: <input type="text" v-model="teamName"> 
          </label>
          <button>Add</button>
        </div>
      </div>
    </form>
      <button @click="handleNext">Done</button>
    <ul>
      <li v-for="team in teams" 
        :key="team.id">
        {{ team.team }}
      </li>
    </ul>
  </div>

</template>

<script>

import { addTeam, addTeamGame, getTeams } from '../services/api';

export default {
  data() {
    return {
      teamName: '',
      teams: null
    };
  },
  props: {

  },
  created() {
    this.gameId = this.$route.params.id;
    getTeams(this.gameId) 
      .then(saved => {
        this.teams = saved;
      });
  },

  methods: {
    handleNext() {
      this.$router.push(`/game/${this.gameId}/instructions`);
    },
    handleSubmit() {
      this.gameId = this.$route.params.id;
      return addTeam(this.teamName)
        .then(saved => {
          this.teams.push(saved)
          this.team = saved;
          addTeamGame(this.team.teamId, this.gameId);
        });
    },
  },

};
</script>

<style scoped>

.team-list {
  display: flex;
  justify-content: space-around;
}

.teams {
  text-align: center;
}

</style>
